@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap);
.sideBar {
    list-style-type: none;
    height: 42%;
    width: 20vh;
    overflow: hidden;
  }

.sideBar img {
    display: inline;
    max-width: 10vh;
    max-width: 10vh;
    padding: 10px 10px;
    margin-top: 10px;
    margin-right: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 01);
  }

.sideBar li {
    display: inline;
    color: #000;
    padding: 10px 10px;
  }

.container1 {
  background-color: rgba(24, 235, 182, 200);
  width: 35%;
  float: left;
  height: 600px;
}
.container2 {
  background-color: whitesmoke;
  width: 65%;
  height: 600px;
  float: right;
}

.container1 img {
  margin-top: 150px;
  margin-left: 60px;
}

.InputForm {
  position: absolute;
  margin-top: 30px;
  margin-left: 200px;
}
.Inputs {
  margin-top: 20px;
}
.formContainer {
  margin-top: 70px;
  position: relative;
  margin-right: 150px;
  background-color: white;
  height: 465px;
  box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
    0px 4px 20px 0 rgba(0, 0, 0, 0.4);
}
.container6 {
  margin-top: 70px;
  margin-left: 70px;
  background-color: rgba(26, 235, 182, 255);
  height: 465px;
  box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
    0px 4px 20px 0 rgba(0, 0, 0, 0.4);
}
.container6 img {
  margin-left: 100px;
}
#element1 {
  margin-left: 100px;
  margin-top: 40px;
}

.Inputs input {
  padding: 12px 20px;
  width: 100%;
}
.Inputs input[type="submit"] {
  padding: 12px 20px;
  background-color: rgba(24, 235, 182, 200);
  width: 120%;
}
.forgetPass {
  float: right;
  font-size: smaller;
}
.newuser {
  padding-right: 30px;
  font-size: smaller;
}

@media only screen and (max-width: 1100px) {
  .container1 {
    background-color: rgba(24, 235, 182, 200);
    width: 40%;
    float: left;
    height: 600px;
  }
  .container2 {
    background-color: whitesmoke;
    width: 60%;
    height: 600px;
    float: right;
  }
  .container6 img {
    margin-left: 10px;
  }
  .InputForm {
    position: absolute;
    margin-top: 10px;
    margin-left: 40px;
  }

  .container1 img {
    margin-top: 150px;
    margin-left: 10px;
  }
  .formContainer {
    margin-top: 40px;
    position: relative;
    margin-right: 50px;
    background-color: white;
    height: 495px;
    box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
      0px 4px 20px 0 rgba(0, 0, 0, 0.4);
  }
  .container6 {
    margin-top: 40px;
    margin-left: 40px;
    background-color: rgba(26, 235, 182, 255);
    height: 495px;
    box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
      0px 4px 20px 0 rgba(0, 0, 0, 0.4);
  }
  .Inputs input {
    padding: 10px 15px;
    width: 100%;
  }
  .Inputs input[type="submit"] {
    padding: 10px 15px;
    background-color: rgba(24, 235, 182, 200);
    width: 120%;
  }
  #element1 {
    margin-left: 90px;
    margin-top: 40px;
  }
}

@media only screen and (max-width: 650px) {
  .container1 {
    background-color: rgba(24, 235, 182, 200);
    width: 45%;
    float: left;
    height: 600px;
  }
  .container2 {
    background-color: whitesmoke;
    width: 55%;
    height: 600px;
    float: right;
  }
  .container6 img {
    margin-left: 0px;
  }
  .InputForm {
    position: absolute;
    margin-top: 10px;
    margin-left: 20px;
  }
  .container1 img {
    margin-top: 150px;
    margin-left: 0px;
  }
  .formContainer {
    margin-top: 40px;
    position: relative;
    margin-right: 20px;
    background-color: white;
    height: 495px;
    box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
      0px 4px 20px 0 rgba(0, 0, 0, 0.4);
  }
  .container6 {
    margin-top: 40px;
    margin-left: 10px;
    background-color: rgba(26, 235, 182, 255);
    height: 495px;
    box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.4),
      0px 4px 20px 0 rgba(0, 0, 0, 0.4);
  }
  .Inputs input {
    padding: 5px 10px;
    width: 85%;
  }
  .Inputs input[type="submit"] {
    padding: 5px 10px;
    background-color: rgba(24, 235, 182, 200);
    width: 100%;
  }
  #element1 {
    margin-left: 80px;
    margin-top: 40px;
  }
}

/** @format */

.firstContainer {
  background-color: rgba(26, 235, 182, 255);
  position: fixed;
}

.secondContainer {
  margin: 20px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(26, 235, 182, 255);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 01), 0 6px 20px 0 rgba(0, 0, 0, 01);
}
.horizontalBar {
  width: 100%;
  height: 17vh;
  list-style-type: none;
  overflow: hidden;
}

.horizontalBar img {
  display: inline;
  max-height: 20vh;
}

.user {
  margin-right: 40px;
  float: right;
  height: 70%;
}
.verticalBar {
  list-style-type: none;
  height: 42%;
  width: 20vh;
}
.verticalBar img {
  display: inline;
  max-width: 10vh;
  max-width: 10vh;
  padding: 10px 10px;
  margin-top: 10px;
  margin-right: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 01);
}
.verticalBar li {
  display: inline;
  color: #000;
}
.logoAndProductContainer {
  display: flex;
}
.productContainer {
  display: flex;
  width: 100%;
  height: 80vh;
  background-color: white;
}
.product {
  margin-top: 15px;
  max-width: 200px;
  max-height: 220px;
  margin: 15px 10px;
  flex-direction: row;
}
.product > p {
  display: inline;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: rgba(26, 235, 182, 255);
  max-width: 200px;
}

.product img {
  max-width: 200px;
  max-height: 200px;
  border: 1px solid rgb(141, 137, 137);
}
/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 800px) {
  .productContainer {
    flex-direction: column;
  }
}

.customLink {
  color: blueviolet;
  cursor: pointer;
}
table,th,td{
    
  margin-left: 10px;
  margin-top: 10px;
  border: 3px solid black;
  border-collapse: collapse;
  padding: 10px;
  width: 30%;
  text-align: center;
  font-weight: bold;
  
}

.textheading{
  color: rgba(26, 235, 182, 255);
  
}

.backcolor{
  background-color:rgba(26, 235, 182, 255);
}

.border{
  border-radius: 25px;
  padding: 20px; 
  width: 200px;
  height: 150px; 
  
}
